<script>
    import { onMount } from 'svelte';
    import io from 'socket.io-client';

    let socket;
    let messages = [];
    let inputMessage = '';

    onMount(() => {
        socket = io();

        socket.on('chat message', (msg) => {
            messages = [...messages, msg];
        });
    });

    function sendMessage() {
        if (inputMessage.trim() !== '') {
            socket.emit('chat message', inputMessage);
            inputMessage = '';
        }
    }
</script>

<main class="container mx-auto p-4">
    <h1 class="text-3xl font-bold mb-4">Real-time Chat</h1>

    <div class="bg-gray-100 p-4 rounded-lg mb-4 h-64 overflow-y-auto">
        {#each messages as message}
            <p class="mb-2">{message}</p>
        {/each}
    </div>

    <form on:submit|preventDefault={sendMessage} class="flex">
        <input
            type="text"
            bind:value={inputMessage}
            placeholder="Type a message..."
            class="flex-grow border rounded-l px-4 py-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
            type="submit"
            class="bg-blue-500 text-white px-4 py-2 rounded-r hover:bg-blue-600 focus:outline-none focus:ring-2 focus:ring-blue-500"
        >
            Send
        </button>
    </form>
</main>

<style global lang="postcss">
</style>
